import React, { useEffect, useState } from "react";
import "./Champion.css";

export enum SizeType {
  Small,
  Large,
  Detail,
  Ban,
}

interface Props {
  size: SizeType;
  src: string;
}

function Champion(props: Props) {
  // var coverSize: string = "0";
  // var coverRadius: string = "0";
  // var imageSize: string = "0";

  // TODO: 컴포넌트 삭제 및 개별 적용
  var [coverStyle, setCoverStyle] = useState("");
  var [imageStyle, setImageStyle] = useState("");

  useEffect(() => {
    switch (props.size) {
      case SizeType.Large:
        setCoverStyle("cover-large");
        setImageStyle("image-large");
        break;
      case SizeType.Small:
        setCoverStyle("cover-small");
        setImageStyle("image-small");
        break;
      case SizeType.Detail:
        setCoverStyle("cover-detail");
        setImageStyle("image-detail");
        break;
      case SizeType.Ban:
        setCoverStyle("cover-ingameBan");
        setImageStyle("image-ingameBan");
        break;
    }
  }, [props.size]);

  return (
    <div className={coverStyle}>
      <img className={imageStyle} src={props.src} alt="" />
    </div>
  );
}

export default Champion;
